import React from 'react';

class SectionFive extends React.Component {
    render() {
        return (
            <div>
                <p>Now the question you've probably been asking, how do I get a car? Well you won't be able to get a nice whip just yet, but you can most definitely afford the best car in the game - the mini cooper 1965. Every player has owned one of these and its an amazing starter car, for only $25,000, it's yours!</p>
                <p>To buy this beauty of a car, go to the car dealer marked on the <a href="#section3">detailed map</a>, right next to bazaar. Go upstairs and speak to the NPC on the right, ask what they have in stock and find the mini cooper under the mini section. I wouldn't recommend buying any different car at this stage, definitely not that shtity ford pickup truck. The mini maxes 35mph and it's just shit but for the price, it's good.</p>
                <p>Once you've purchased your lovely new car, go to the NPC at the bottom of the stairs to rice out your car; change colour, gloss and whatever else you want. This can be expensive so be careful not to spend all your drug money. Your car will already be spawned now but to spawn it, go to the NPC out the front and spawn it. Inside the city garage next to bank, you can also spawn it at the NPC.</p>
                <p> Your car also has another cool feature, if you click T while looking at it you can open the car storage. In here you can legally store weapons, drugs and whatever else. There is limited space in here but enough to store a gun and a spare magazine.</p>
            </div>
        )
    }
}

export default SectionFive;