import React from 'react';
class SectionEight extends React.Component {
    render() {
        return (
            <div>
                <p>In this section you will want to refer to the <a href="#section3">detailed map</a>. All of these locations are marked on it and I will just describe their use.</p>
                <ul>
                    <li>Fredy's Bakery - There are two of these shops, one in the City and one in the Suburbs. In this shop you can buy the best valued and most effective food; burgers.</li>
                    <li>Jennifer's Clothing - In this shop you can buy a makeover, jewlery and drip (premium only) and if you go upstairs, you can change your clothes and customize your character however you want. There are two Jennifers', one in Business Sector and one in Jennifer's just up the road from Fredy's Bakery.</li>
                    <li>Suburbs Storage - Between Fredy's Bakery and Jennifer's in the Suburbs, you there is an NPC which when you talk to them, lets you access your storage. This is great for quickly withdrawing items like weapons without having to own a property.</li>
                    <li>Roadcrew Station - In the RC Station you can repair your car and buy useful chemicals like Lye, which can be used in cocaine and meth production.</li>
                    <li>Gas Stations - Gas stations always provide an ATM, a place to refuel, food and water.</li>
                    <li>City Garage - Down the road from the bank, the big car park is host to a car spawner and a monorail station, so you can get around with ease.</li>
                    <li>The Pub - By the docks there is a little pub in which you can buy cigarettes, a fishing pole, beer and a bong. Everything you need right?</li>
                </ul>
            </div>
        )
    }
}

export default SectionEight;