import React from 'react';
import { Heading } from '../titles';

class SectionSeven extends React.Component {
    render() {
        return (
            <div>
                <p>So I'll assume you've followed the guide up until now and are now at least level 4 crafting, have a mini cooper, some money left and a property (preferably bazaar shop). Good!</p>
                <p>Crafting is very simple, especialy with the right tools. Whenever you want to craft, I recommend you go to <a rel="noopener noreferrer" target="_blank" href={"http://calc.tinycoding.com/"}>this website</a> (TinyCalc), its an incredible website that helps you to craft any amount of items and show you the exact price. I would also recommend you buy a "Book of Law" from the medical shop opposite bank, if you then left click this in your inventory you can see all the taxes. On TinyCalc, enter in the current sales tax and you will then have a fully accurate crafting calculator.</p>
                <p>In crafting baseball bats earlier, you will have a higher firearms level. The firearms level determines what weaponry you can craft, less powerful guns take less firearms and the much better, or high caliber, guns cost up to 125. On TinyCalc you can search for items and it will tell you what you need to make them and other info. Buy up the materials to craft some baseball bats then craft them in your bazaar shop and store them in your chest in the back of the shop. You can store as many items in here as you want so it's great for bats. When you are firearms level 3 you can craft pistol ammo, which is a great thing to craft. To make ammo, go to mixtures as you would, then go to the "Ammunition" tab and click "Pistol Ammo".</p>
                <Heading title="crafting your first gun" else="guns" />
                <p>When you reach level 5 firearms, you are ready to make your first gun. Search "Walther PPK" into TinyCalc, press the $ icon then search "7.65x17 magazine" and do the same. You will now have the materials on-screen for the Walther PPK and its magazine. You can now buy these materials and craft it as you would a baseball bat but instead of going to "Melee" or "Ammunition", go to "Weaponry" and scroll to the bottom. You will see it flashing green. Once you have crafted this gun, go to the "Magazines" tab and craft the "7.65x17 magazine", which should be flashing green. Assuming you have already crafted some pistol ammo; you need to get the pistol ammo out of your storage, in your inventory left click on the magazine to fill it with bullets. When full, with your gun equipped press left click once and then press R. Your gun will now be loaded, to lower the weapon (put it on safety) press E + Left Click. Be aware, shooting your gun in a property can warrant a ticket from the police so be careful when and where you use your weapon.</p>
                <p>Great, you have a gun and you know how to load it and put it on safety; next!</p>
                <Heading title="basic combat" else="combat" />
                <p>Gunfights are some on the most fun experiences on PERPHeads, they can be high stake and exhilerating. Gunfights do not happen lightly though, as defined by rule 3.4, you must not put your life at risk; this definitely means don't just start a gunfight for gun. I imagine you also know the concept of RDM, that comes under rule 2.5 (excessive negativity). You need to have a justified reason to shoot someone or you risk being banned. For example, if someone punches you, you can't then shoot them. An example of a justified reason to shoot someone is if they attempt to raid your property with weapons, you are then fully permitted to shoot them under self defenced, justified by section 2 of the paralake penal code and the rules.</p>
                <p>When in a gunfight, you should always aim for the head. Depending on which gun you're using, a body shot may not be as effective; especially if they're using a more high caliber weapon. As it stands, a Walther PPK cannot 1 tap people, though every other gun can. A 1 tap is simply when someone dies from one shot. When someone is "dead", they aren't fully dead. Sounds complicated but it's not; someone is still alive when they are down and moaning, they are just "red screened", unconscious. To properly kill (black screen) someone, you shoot them again until they stop moaning. Then they are dead.</p>
                <p>When you are "red screened" you still have a chance of being revived, however when you're "black screened" you will need to wait another 4 minutes before being alive again and a further 5 minutes before interacting with the situation.</p>
                <p>When you are shot, stabbed, hit with a bat too many times or fall from a height; you bleed. The only way to stop bleeding before it kills you is to use a bandage, which you can buy from the medical shop opposite bank. After having bled and bandaged yourself, you will likely be "crippled", this means you walk slowly and are forced to be healed by either a stim pack, medic or go to the back of hospital and pay to be healed.</p>
                <p>Before going into a gunfight, I recommend getting bandages because you never know what's going to happen!</p>
            </div>
        )
    }
}

export default SectionSeven;