import React from 'react';
import { Map } from '../../styles';
import intromap from '../../img/maps/intromap.jpg'

class SectionOne extends React.Component {
    render() {
        return (
            <div>
                <p>So you've just spawned in? You've picked your character and you're ready to begin your adventure in Paralake! There's a few places you'll need to go before you start trying to make all that money, which don't worry, will come soon! Quick note: Press F1 on your keyboard to view the interaction menu, here you can view rules, skills and other juicy info.</p>
                <p>Here's a map of Paralake, I'll explain where to go below.</p>
                <Map src={intromap} />
                <p>You'll firstly want to make your way to either of the yellow dots, quick tip: holding ALT will let you sprint. The yellow dot straight ahead down 'Shady Street' is closest. These are gas stations in which you can buy a cellphone, water and a flashlight. These are 3 good items to have, water is optional but it increases your health and is definitely recommended. I personally always carry a flashlight and it goes without saying that you will need a phone. When you spawn in you should have enough money to purchase these items, if for some reason you do not then you can go to either of the ATMs marked in green on the map and withdraw whatever money you need.</p>
                <p>To check how much money you have on you, hold Q which will open your inventory, which should currently contain nothing. You shouldn't carry lots of money around with you at most given times as you could be mugged and lose it all.</p>
                <p>At most given times; or when there isn't a mayor, a cellphone will cost you $625 (25% tax) however this price varies depending on taxes, which we'll go into later. You can also buy a water bottle for $187.5 and a flashlight for $1,000, once again, this is prone to change along with taxes.</p>
                <p>You next want to make your way to the orange dot, that is a Fredy's bakery and is only a short walk from City Hall (spawn). In here you can buy Burgers which are the best valued and probably best source of food. At any given time you can carry 15 food/water, so you'll want to moderate. I would recommend 10-12 burgers then 3-5 water.</p>
                <p>When you've stocked up on food, bought a smartphone and possibly a flashlight, you can press Z to open up your phone and do all kinds of cool things. If you hold Q again, you will see your food and phone (and water if you bought any) in your inventory. To consume food or water, click on it while holding Q.</p>
                <p>You're now ready to move on!</p>
            </div>
        )
    }
}

export default SectionOne;