import React from 'react';
import Card from 'react-bootstrap/Card';
import 'bootstrap/dist/css/bootstrap.min.css';
import Carousel from 'react-bootstrap/Carousel';
import delivery from '../img/icons/delivery.jpg';
import fire from '../img/icons/fire.jpg';
import medic from '../img/icons/medic.jpg';
import police from '../img/icons/police.jpg';
import rc from '../img/icons/rc.jpg';
import ss from '../img/icons/ss.jpg';
import taxi from '../img/icons/taxi.jpg';
import { Required } from '../styles';

class JobCarousel extends React.Component {
    render() {
        return (
            <Carousel indicators={false}>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={fire} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Firefighter<Required>*</Required></Card.Title>
                            <p>In this job you are paid to extinguish fires around the city. These fires can be caused by anything between a car blowing up to a firework being lit!</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={medic} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Paramedic<Required>*</Required></Card.Title>
                            <p>This is a very well paying job, in this job you are on call in ambulance with which respond to calls, revive people, dispose of bodies and heal people. You get paid every time you dispose of a body or revive someone.</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={ss} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Secret Service<Required>*</Required></Card.Title>
                            <p>In this job your only task is to protect the Mayor. You are issued different guns to choose between; a colt 1911; a glock 18c or a HK USP. I personally use the glock 18c as if you press E + Right click it has an automatic fire mode!</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={rc} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Road Crew<Required>*</Required></Card.Title>
                            <p>In this job you are tasked with road maintenance. You tow, repair and impound cars. Roadcrew can be called with 565 on the phone</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={delivery} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Courier</Card.Title>
                            <p>In this job you deliver parcels to different shop owners and properties for money.</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={taxi} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Taxi Driver</Card.Title>
                            <p>In this job your only task is to protect the Mayor. You are issued different guns to choose between; a colt 1911; a glock 18c or a HK USP. I personally use the glock 18c as if you press E + Right click it has an automatic fire mode!</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
                <Carousel.Item>
                    <Card className="card-custom">
                        <Card.Img variant="top" src={police} />
                        <Card.Body className="custom-card-body">
                            <Card.Title>Police Officer</Card.Title>
                            <p>This is a whitelisted job, meaning you will have to apply for it. To apply for Officer, go to <a href="https://plpd.online">plpd.online</a>. In this job, you are tasked with enforcing the law and are given equipment and vehicles to help you do so!</p>
                        </Card.Body>
                    </Card>
                </Carousel.Item>
            </Carousel>
        )
    }
}

export default JobCarousel;