import React from 'react';
import { MutedText, Map } from '../../styles';
import fullmap from '../../img/maps/fullmap.jpg'

class SectionThree extends React.Component {
    render() {
        return (
            <div>
                <p>This is a very short one, it just features a detailed map of the city.</p>
                <Map src={fullmap} />
                <MutedText>credit to @rogue</MutedText>
            </div>
        )
    }
}

export default SectionThree;