import React from 'react';
import { sections } from '../variables';

class Section extends React.Component {
    render() {
        return (
            <div id={`section${this.props.num}`}>
                <h5 className="heading">SECTION {this.props.num} - {sections["section" + this.props.num].title} <a className="jumpAnchor" id={`jumpAnchor${this.props.num}`} href={"#section" + this.props.num}>#</a></h5>
            </div >
        );
    }
}

class Heading extends React.Component {
    render() {
        return (
            <div id={`${this.props.else}`}>
                <h5 className="heading">{this.props.title} <a className="jumpAnchor" id={`jumpAnchor${this.props.else}`} href={"#" + this.props.else}>#</a></h5>
            </div>
        );
    }
}

export { Heading, Section }