import React from 'react';
import { Heading } from '../titles';

class SectionTwo extends React.Component {
    render() {
        return (
            <div>
                <p>The next important thing, your statistics. If you hold Q and look to the left of the menu, you will see your player information and skills. I wouldn't expect any of your levels to be anything above 1 at this point but we'll go into them now.</p>
                <Heading title="player information" else="playerinfo" />
                <p>The player information is as follows:</p>
                <ul>
                    <li>Health - This changes when you take damage, it regenerates on its own (affected by the regeneration gene) or you can use a STIM pack to heal yourself, but you won't have those just yet.</li>
                    <li>Armour - This can only change if you go on a government job such as Police or Secret Service.</li>
                    <li>Hunger - This will decrease as time goes by, to get it back up, eat food. When your hunger hits "extremely hungry" your vision will become blurry.</li>
                    <li>Stamina - This decreases as you run around, it affects your regeneration and means you cannot run as fast, or at all. To get this back up, drink water.</li>
                </ul>
                <Heading title="skills" else="skills" />
                <p>Your skills level up as you play, they've pretty self-explanatory, for example; your stamina skill will level up from running around a lot. The only skills that I imagine will raise confusion are the firearms and woodworking skills that level up from crafting, which we'll go into later. </p>
                <Heading title="genetics" else="genes" />
                <p>Genetics are the last thing we'll go over in this section. To level up your genetics, go left from "City Hall" (Spawn), go over the bridge or under the tunnel, NOT THROUGH THE ROAD! and to your left will be the hospital. Inside the hospital, talk to the first NPC you see with E and ask about genetics. You will have some points to start off with but once you've used them up, you will have to buy more which can be expensive.</p>
                <p>The genes you can level are as follows:</p>
                <ul>
                    <li>Regeneration - This is a good skill, it affects how fast you get back health when taking damage.</li>
                    <li>Perception - This is good for crafting, I wouldn't bother with it straight away.</li>
                    <li>Influence - Useless. Don't bother.</li>
                    <li>Intelligence - Needed for crafting. I would at least spend 1 of your points on this.</li>
                    <li>Strength - This affects your unarmed combat; e.g. punching, crafting, amount of weight you can carry and how much damage you take. I would invest a point in this.</li>
                    <li>Honestly not that useful, unless you want car bombs!</li>
                </ul>
                <p>Here is an ideal setup:</p>
                <ul>
                    <li>Regeneration 2</li>
                    <li>Perception 4</li>
                    <li>Influence 0</li>
                    <li>Intelligence 4</li>
                    <li>Strength 3</li>
                    <li>Dexterity 2</li>
                </ul>
            </div>
        )
    }
}

export default SectionTwo;