import React from 'react';
import { Heading } from './titles';

class About extends React.Component {
    render() {
        return (
            <div>
                <Heading title="about" else="about" />
                <p>The aim of this website is to provide a detailed starter guide for all new players to the PERPHeads community. I aim to give a visually pleasing and complete guide to the gamemode and how to get started!</p>
                <p>To make it easier to navigate, this site will be divided into sections which will give lots of detail about the topic of said section. To jump straight to a section, click one of the links to the right of the page.</p>
                <p>The reason I have made this as a website instead of a forum post is I want to make it my own, change it how I like and make it with user experience in mind. This site was designed to be fully responsive and accessible on any device.</p>
                <p>Anyway, let's get started!</p>
            </div >
        )
    }
}

export default About;